import Map from 'ol/Map';
import View from 'ol/View';
import {layers, explorer} from './layers/';

const zoom = 8.5;
const center = [-4101693,-796400];
const projection = 'EPSG:900913';

const map = new Map({
   layers: layers,
    view: new View({
      projection: projection,
      center: center,
      zoom: zoom,
      maxZoom:18
    }),
    controls: [],
  });

  export {map, zoom, center, projection, explorer};
