import './LocateMe.css';
import { vector } from '../../_config/layers/';
import React, { useEffect, useRef, useState } from 'react';
import Feature from 'ol/Feature';
import Point from 'ol/geom/Point';
import Geolocation from 'ol/Geolocation';
import { Circle as CircleStyle, Fill, Stroke, Style } from 'ol/style';
import locateme_min from '../../_assets/img/locateme_min.png';
import {zoom, center} from '../../_config/map';

function LocateMe(props) {

  const element = useRef();

  const geolocation = new Geolocation({
    // enableHighAccuracy must be set to true to have the heading value.
    trackingOptions: {
      enableHighAccuracy: true,
    },
    projection: props.map.getView().getProjection(),
  });

  const positionFeature = new Feature();

  const accuracyFeature = new Feature();

  let firstClick = true;

  useEffect(() => {
    
    geolocation.on('change:accuracyGeometry', function () {
      try {
        accuracyFeature.setGeometry(geolocation.getAccuracyGeometry());
        vector.getSource().addFeature(accuracyFeature)
      } catch (ex) {
        console.log(ex)
      }
    });

    positionFeature.setStyle(
      new Style({
        image: new CircleStyle({
          radius: 6,
          fill: new Fill({
            color: '#3399CC',
          }),
          stroke: new Stroke({
            color: '#fff',
            width: 2,
          }),
        }),
      })
    );

    geolocation.on('change:position', function () {
      try {
        const coordinates = geolocation.getPosition();
        if(firstClick){
          props.map.getView().setCenter(coordinates);
          props.map.getView().setZoom(15);
          firstClick = false;
        }
        positionFeature.setGeometry(coordinates ? new Point(coordinates) : null);
        vector.getSource().addFeature(positionFeature)
      } catch (ex) {
        console.log(ex)
      }
    });

  }, [])

  const handleClick = () => {

    if(!geolocation.getTracking()){
      
      geolocation.setTracking(true);
      
    }
    else{
      geolocation.setTracking(false);
      positionFeature.setGeometry(null);
      accuracyFeature.setGeometry(null);
      props.map.getView().setZoom(zoom);
      props.map.getView().setCenter(center);
    }

  }

  return (
    <div className="LocateMe">
      <input type='image' alt='Rastrear a sua localizaÃ§Ã£o' src={locateme_min} title='Rastrear a sua localizaÃ§Ã£o' onClick={handleClick} ref={element}></input>
    </div>
  )

}

export default LocateMe;