import React, {useEffect, useRef, useState} from 'react';
import './Folder.css';
import openedFolder from '../../../../_assets/img/openedFolder.png';
import closedFolder from '../../../../_assets/img/closedFolder.png';


const Folder = (props) => {

  const content = useRef(null);

  const titleUnicode = useRef(null);

  const [toggle, setToggle] = useState(false);

  const handleFolderClick = () => {

    if(toggle){
      content.current.style.visibility = 'hidden';    

      content.current.style.display = 'none';

      content.current.style.opacity = '0'; 

      setToggle(false);
    }else{
      content.current.style.visibility = 'visible';
      
      content.current.style.display = 'inherit';

      content.current.style.opacity = '1'; 

      setToggle(true);
    }
  }

  const isScoped = () => {

    if(content.current.style.visibility===`visible`){
      setToggle(true);
    }else{
      setToggle(false);
    }
  }

  return (
      <div onMouseOver={isScoped} className='folder' id={`folder${props.folderIndex}`}> 
        <h3 className='title' onClick={handleFolderClick}><div><img alt="Pasta" ref={titleUnicode} id={`folder${props.folderIndex}image`} src={toggle?openedFolder:closedFolder}/></div> {props.title}</h3>
        <div ref={content} className='content'>
          {props.children}
        </div>
      </div>
    );
}


export default Folder;
