import './Welcome.css';
import React, { useRef } from 'react';
import MapWrapper from '../MapWrapper/';
import ScaleBar from '../ScaleBar/';
import MouseWatcher from '../MouseWatcher';
import Header from '../../containers/Header';
import Navbar from '../Navbar/';
import LayerSwitcher from '../LayerSwitcher/';
import ResetZoom from '../ResetZoom';
import LocateMe from '../LocateMe';
import HintWindow from '../HintWindow/';
import BaseLayer from '../BaseLayer/';
import GridLayer from '../GridLayer/';
import Toolbar from '../Toolbar/';
import { map, explorer } from '../../_config/map';

const Welcome = () => {

  const collapseSwitcher = true;

  const switcherRef = useRef();

  const clientWidth = document.documentElement.clientWidth;

  return (
    <div className="Welcome">
      <Header>
        <Navbar map={map} switcherRef={switcherRef} >
          {clientWidth > 900 ? (<Toolbar map={map} />) : null}
        </Navbar>
      </Header>
      <MapWrapper map={map}>
        <ScaleBar map={map} />
        <MouseWatcher map={map} />
        <ResetZoom map={map} />
        <BaseLayer map={map} />
        <GridLayer map={map} />
        <LocateMe map={map} />
        
        <LayerSwitcher map={map} switcherRef={switcherRef} explorer={explorer} collapsed={collapseSwitcher} />
        {clientWidth <= 900 ? (<Toolbar map={map} />) : null}
      </MapWrapper>
    </div>
  )
}

export default Welcome;